<template>
  <div id="app">
   <el-tabs v-show="navshow" v-model="activeName" @tab-click="handleClick" style="width: 60%;float: left">
    <el-tab-pane label="评分表" name="score"></el-tab-pane>
    <el-tab-pane label="作业表" name="image"></el-tab-pane>
  </el-tabs>
    <div id="time" v-show="timeshow" style="display: flex;float: right;margin-top: 15px;color: #f56c6c;font-size: 24px;">
      <transition name="fade">
        <div class="right-bar-item primary" style="top: 16px">
          <div class="value">
            <el-icon name="time" style="margin-right: 8px;" /> {{ date }}
          </div>
          <div class="name">
            <el-button type="danger" :icon="status==='开始'?'el-icon-video-play':'el-icon-video-pause'" size="small" style="margin-left: 20px;margin-right: 10px" @click="stopDate">
              {{ status }}</el-button>
          </div>
        </div>
      </transition>
    </div>
    <br>
    <br>
    <keep-alive>
      <router-view />
    </keep-alive>
  </div>
</template>

<script>

export default {
  data(){
  return {
    activeName:'first',
    clientId: 111,
    status:'暂停',
    stim:null
  }


},
  computed:{
    date(){
      return this.$store.state.date
    },
    datevalue(){
      return this.$store.state.datevalue
    },
    stoptime(){
      return this.$store.state.stoptime
    },
    tid(){
      return this.$store.state.tid
    },
    sid(){
      return this.$store.state.sid
    },
    navshow(){
      return this.$store.state.navshow
    },
    timeshow(){
      return this.$store.state.timeshow
    }

  },

  methods:{
  handleClick() {
        if(this.activeName==='score'){
          this.$router.push({ path: "/score" });
        }
        if(this.activeName==='image'){
          this.$router.push({ path: "/image" });
        }
    },
    stopDate(){
      // eslint-disable-next-line no-unused-vars

    if (this.$store.state.isstop===false){

      this.status = "开始"
      this.$store.state.isstop=true
      this.stime = setInterval(()=>{
        this.$store.state.stoptime += 1
      },1000)
      var stop_data = {
        'mess':'stop',
        'time':this.$store.state.date
      }
      this.$send('tid:'+this.tid, JSON.stringify(stop_data))

    }else {
      this.$store.state.isstop=false
      this.status = "暂停"
      clearInterval(this.stime)
      // eslint-disable-next-line no-unused-vars
      var start_data = {
        'mess':'start',
        'time':this.$store.state.stoptime
      }
      this.$send('tid:'+this.tid,JSON.stringify(start_data))
    }
    },
    mqttMSG() {

    },


}
}

</script>
