import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import preview from 'vue-photo-preview';
import 'vue-photo-preview/dist/skin.css';
import VueSignature from '../node_modules/vue-signature-simple';
// import jquery from 'jquery';
import router from './router'
import store from './store'

import mqtt from 'mqtt'
import { MQTT_SERVICE, MQTT_USERNAME, MQTT_PASSWORD } from './util/sysconstant'
// eslint-disable-next-line no-unused-vars
var client
// eslint-disable-next-line no-unused-vars
const options = {
  connectTimeout: 40000,
  username: MQTT_USERNAME,
  password: MQTT_PASSWORD,
  clean: true
}
client = mqtt.connect(MQTT_SERVICE, options)

Vue.prototype.$conn = function ($tid, $sid) {
  // mqtt连接
  client.on('connect', () => {
    console.log('连接成功:')
    client.subscribe('sid:' + $sid, {qos: 1}, (error) => {
      if (!error) {
        console.log('订阅成功')
        client.publish('tid:'+ $tid,"connect")
      } else {
        console.log('订阅失败')
      }
    })
  })
  // 接收消息处理
  client.on('message', (topic, message) => {
    console.log('收到来自', topic, '的消息', message.toString())
    this.msg = message.toString()
  })
  // 断开发起重连
  client.on('reconnect', (error) => {
    console.log('正在重连:', error)
  })
  // 链接异常处理
  client.on('error', (error) => {
    console.log('连接失败:', error)
  })
}

Vue.prototype.$send = function (topic,message) {
  //发布消息@topic主题  @message发布内容
    console.log(client.connected)
    if (!client.connected) {
      console.log('客户端未连接')
      return
    }
    client.publish(topic,message,{qos: 1},(err) => {
      if(!err) {
        console.log('主题为'+topic+ "发布成功")
      }
    })
}

var option = {
  // maxSpreadZoom: 1, // 控制预览图最大的倍数，默认是2倍，我这里改成了原图
  fullscreenEl: false, //控制是否显示右上角全屏按钮
  // closeEl: false, //控制是否显示右上角关闭按钮
  // tapToClose: true, //点击滑动区域应关闭图库
  // shareEl: false, //控制是否显示分享按钮
  // zoomEl: false, //控制是否显示放大缩小按钮
  // counterEl: false, //控制是否显示左上角图片数量按钮
  // arrowEl: true,  //控制如图的左右箭头（pc浏览器模拟手机时）
  // tapToToggleControls: true, //点击应切换控件的可见性
  // clickToCloseNonZoomable: true //点击图片应关闭图库，仅当图像小于视口的大小时
}
Vue.config.productionTip = false
// Vue.use(jquery);
Vue.use(ElementUI);
Vue.use(VueSignature);
Vue.use(preview, option)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
